<template>
	<view>
		<view class="uni-margin-wrap">
			<swiper class="swiper" circular :indicator-dots="false" :autoplay="true" interval="2000" duration="300">
				<swiper-item v-for="(item,index) in items.swiper.items" :key="'swiper-' + index">
					<image mode="widthFix" class="w-100" :src="item.image"></image>
				</swiper-item>
			</swiper>
		</view>
		
		<Search></Search>

		<view class="d-flex justify-content-between align-items-center p-3">
			<view class="fs-5">精选推荐</view>
			<view class="fs-6">
				<view class="d-flex justify-content-start align-items-center">
					<image style="height: 24px;" src="/static/icons/gift.svg"></image>
					分享有礼
				</view>
			</view>
		</view>
		<view>
			<uni-grid :column="2" :showBorder="false" :square="false">
				<uni-grid-item v-for="(item,index) in items.recommend" :key="'recommend-' + index">
					<view class="bg-body-secondary bg-opacity-75 rounded-3 m-2 p-3">
						<view class="d-flex justify-content-start align-items-center">
							<image style="width: 24px; height: 24px;" mode="heightFix" :src="item.icon"></image>
							<text class="fw-bold ms-2">快捷写作</text>
						</view>
						<view class="gray fw-light mt-2 f12">{{item.description}}</view>
					</view>
				</uni-grid-item>
			</uni-grid>
		</view>

		<view class="bg-primary-subtle bg-opacity-50 rounded-3 m-2 p-2" v-for="(v,i) in items.taxonomy" :key="'taxonomy-' + i">
			<view class="d-flex justify-content-between align-items-center p-2">
				<view class="fs-5">{{v.name}}</view>
				<view class="fs-6">查看更多</view>
			</view>
			<uni-grid :column="2" :showBorder="false" :square="false">
				<uni-grid-item v-for="(vv,ii) in v.items" :key="'tool-' + ii">
					<view class="bg-light rounded-3 m-2 p-2">
						<view class="d-flex justify-content-between align-items-center">
							<text class="fw-bold ms-2">{{vv.name}}</text>
							<image style="width: 24px; height: 24px;" mode="heightFix" :src="vv.icon"></image>
						</view>
						<view class="gray fw-light mt-2 f12">{{vv.description}}</view>
					</view>
				</uni-grid-item>
			</uni-grid>	
		</view>
	</view>
</template>

<script>
	import {mapActions} from 'pinia'
	import {homeStore} from '@/stores/home'
	import Search from '@/components/search'
	export default {
		components: {
			Search
		},
		data() {
			return {
				items: {}
			}
		},
		onLoad() {
			this.fetch()
		},
		onPullDownRefresh() {
			this.fetch()
		},
		methods: {
			...mapActions(homeStore, ['homeData']),
			fetch() {
				this.homeData().then(resp => {
					this.items = resp.data
					console.log('items', this.items)
					uni.stopPullDownRefresh()
				})
			}
		}
	}
</script>

<style>
	.uni-card__header {
		border-bottom: 0 !important;
	}
</style>
